@import url(https://fonts.googleapis.com/css?family=Quicksand:400,500,700);
html {
	line-height: 1.15;

	-ms-text-size-adjust: 100%;

	-webkit-text-size-adjust: 100%;
}

body {
	margin: 0;
}

/**
   * Add the correct display in IE 9-.
   */
