* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}
:root {
	--primaryColor: #f48247;
	--mainWhite: #fff;
	--offWhite: #f7f7f7;
	--mainBlack: #222;
	--mainGrey: #ececec;
	--darkGrey: #cfcfcf;
	--mainTransition: all 0.3s linear;
	--mainSpacing: 3px;
	--lightShadow: 2px 5px 3px 0px rgba(0, 0, 0, 0.5);
	--darkShadow: 4px 10px 5px 0px rgba(0, 0, 0, 0.5);
}
/* globals */
body {
	/* padding-top: 66px; */
	color: #222;
	color: var(--mainBlack);
	background: #fff;
	background: var(--mainWhite);
	font-family: Verdana, Geneva, Tahoma, sans-serif;
	line-height: 1.4;
}

.fontSize {
	font-size: 1.4rem;
}


input[type="color"],
input[type="date"],
input[type="datetime"],
input[type="datetime-local"],
input[type="email"],
input[type="month"],
input[type="number"],
input[type="password"],
input[type="search"],
input[type="tel"],
input[type="text"],
input[type="time"],
input[type="url"],
input[type="week"],
select:focus,
textarea {
  font-size: 16px !important;
}

h1 {
	font-size: 3em;
	line-height: 1;
	margin-bottom: 0.5em;
	color: #f7f7f7;
}
h2 {
	font-size: 2em;
	margin-bottom: 0.75em;
}
h3 {
	font-size: 2em;
	line-height: 1;
	margin-bottom: 1em;
}
.font-small {
	background-color: black;
}
h4 {
	font-size: 2.2em;
	line-height: 1.25;
	margin-bottom: 1.25em;
}
h5 {
	font-size: 1em;
	font-weight: bold;
	margin-bottom: 1.5em;
}
h6 {
	font-size: 1em;
	font-weight: bold;
	margin-bottom: 1.5em;
}

.btn-primary {
	display: inline-block;
	text-decoration: none;
	letter-spacing: 3px;
	letter-spacing: var(--mainSpacing);
	color: #fff;
	background: #f48247;
	background: var(--primaryColor);
	padding: 1.4rem 1.9rem;
	/* border: 3px solid var(--primaryColor); */
	transition: all 0.3s linear;
	transition: var(--mainTransition);
	text-transform: uppercase;
	cursor: pointer;
	border-color: #d9534f;
}
.btn-primary:hover {
	background: #f48247;
	background: var(--primaryColor);
	color: #f7f7f7;
	border-color: #d9534f;
}
.loading {
	text-transform: capitalize;
	text-align: center;
	margin-top: 3rem;
}
.error {
	text-align: center;
	text-transform: uppercase;
	margin: 2rem 0;
}
.empty-search {
	text-align: center;
	text-transform: capitalize;
	margin: 2rem 0;
	padding: 1rem;
	letter-spacing: 3px;
	letter-spacing: var(--mainSpacing);
}
/* end of globals */
/* Navbar */
.navbar {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	padding: 0.75rem 2rem;
	background: #f7f7f7;
	background: var(--offWhite);
	z-index: 1;
}
.nav-header {
	display: flex;
	justify-content: space-between;
}
.nav-btn {
	background: transparent;
	border: none;
	cursor: pointer;
	outline: none;
}
.nav-icon {
	font-size: 1.5rem;
	color: #f48247;
	color: var(--primaryColor);
}
.nav-links {
	height: 0;
	overflow: hidden;
	transition: all 0.3s linear;
	transition: var(--mainTransition);
	list-style-type: none;
}
.nav-links a {
	display: block;
	text-decoration: none;
	padding: 1rem 0;
	color: #222;
	color: var(--mainBlack);
	transition: all 0.3s linear;
	transition: var(--mainTransition);
	text-align: center;
	font-size: 1rem;
	font-weight: 600;
	letter-spacing: 3px;
	letter-spacing: var(--mainSpacing);
}
.nav-links a:hover {
	color: #f48247;
	color: var(--primaryColor);
}

.show-nav {
	height: 100px;
}
@media screen and (min-width: 768px) {
	.nav-btn {
		display: none;
	}
	.nav-center {
		max-width: 1170px;
		margin: 0 auto;
		display: flex;
	}
	.nav-links {
		height: auto;
		display: flex;
		margin-left: 4rem;
	}
	.nav-links a {
		margin: 0 1rem;
		padding: 0.5rem 0;
	}
}
/* end of navbar */
/* Hero */
.defaultHero,
.roomsHero {
	min-height: calc(100vh - 66px);
	background: url(/react-deploy/static/media/bg2.14b76cb2.jpg) center/cover no-repeat;
	display: flex;
	align-items: center;
	justify-content: center;
}
.roomsHero {
	background-image: url(/react-deploy/static/media/roomsbg.6b1a770f.jpg);
	min-height: 60vh;
}
/* End of Hero */
/* Banner */
.banner {
	display: inline-block;
	background: rgba(0, 0, 0, 0.5);
	color: #fff;
	color: var(--mainWhite);
	padding: 2rem 1rem;
	text-align: center;
	text-transform: capitalize;
	letter-spacing: 3px;
	letter-spacing: var(--mainSpacing);
}
.banner h1 {
	font-size: 2.5rem;
}
.banner div {
	width: 16rem;
	height: 2px;
	background: #f48247;
	background: var(--primaryColor);
	margin: 1.7rem auto;
}
.banner p {
	font-size: 1.2rem;
	margin-bottom: 2rem;
}
@media screen and (min-width: 576px) {
	.banner {
		padding: 2rem 3rem;
	}
	.banner h1 {
		font-size: 3rem;
	}
}
@media screen and (min-width: 992px) {
	.banner {
		padding: 2rem 6rem;
	}
	.banner h1 {
		font-size: 4rem;
	}
}
/* End of Banner */
/* Title */
.section-title {
	text-align: center;
	margin-bottom: 4rem;
}
.section-title h4 {
	font-size: 3.5rem;
	letter-spacing: 3px;
	letter-spacing: var(--mainSpacing);
	text-transform: capitalize;
	margin-bottom: 1rem;
}
.section-title div {
	width: 5rem;
	height: 5px;
	margin: 0 auto;
	background: #f48247;
	background: var(--primaryColor);
}
/* end of Title */

/* services */
.services {
	padding: 5rem 0;
}
.services {
	background: #cfcfcf;
	background: var(--darkGrey);
	text-align: center;
}
.services-center {
	width: 90vw;
	margin: 0 auto;
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(255px, 1fr));
	grid-row-gap: 2rem;
	grid-column-gap: 50px;
}
.service span {
	display: inline-block;
	color: #f48247;
	color: var(--primaryColor);
	font-size: 2.5rem;
	margin-bottom: 1.5rem;
}
.services h6 {
	letter-spacing: 3px;
	letter-spacing: var(--mainSpacing);
}
.services p {
	width: 80%;
	margin: 0 auto;
}
@media screen and (min-width: 992px) {
	.services-center {
		width: 95vw;
		max-width: 1170px;
	}
}

@media screen and (min-width: 1200px) {
	.services p {
		width: 100%;
	}
}
/*end of services */
/* featured rooms */

.featured-rooms {
	padding: 5rem 0;
}
.featured-rooms-center {
	width: 80vw;
	margin: 0 auto;
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(270px, 1fr));
	grid-row-gap: 2rem;
	grid-column-gap: 40px;
}
@media screen and (min-width: 776px) {
