}
/* end pf featured rooms */
/* room */
.room {
	box-shadow: 2px 5px 3px 0px rgba(0, 0, 0, 0.5);
	box-shadow: var(--lightShadow);
	transition: all 0.3s linear;
	transition: var(--mainTransition);
}
.room:hover {
	box-shadow: 4px 10px 5px 0px rgba(0, 0, 0, 0.5);
	box-shadow: var(--darkShadow);
}

.img-container {
	position: relative;
}
.img-container img {
	width: 100%;
	display: block;
	transition: all 0.3s linear;
	transition: var(--mainTransition);
}
.price-top {
	position: absolute;
	top: 0;
	left: 0;
	background: #f48247;
	background: var(--primaryColor);
	color: #fff;
	color: var(--mainWhite);
	padding: 0.8rem 1.6rem 0.1rem 1.2rem;
	border-bottom-right-radius: 1rem;
	font-size: 1rem;
	text-align: center;
	transition: all 0.3s linear;
	transition: var(--mainTransition);
}
.price-top h6 {
	margin-bottom: 0;
	font-size: 1rem;
	font-weight: 300;
	color: #fff;
	letter-spacing: 3px;
	letter-spacing: var(--mainSpacing);
}
.room-link {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: scale(0);
	transition: all 0.3s linear;
}
.img-container:hover {
	background: rgba(0, 0, 0, 0.8);
}
.img-container:hover img {
	opacity: 0.3;
}
.img-container:hover .price-top {
	opacity: 0;
}
.img-container:hover .room-link {
	transform: translate(-50%, -50%) scale(1);
}
.room-info {
	background: #2c3335;
	text-transform: capitalize;
	padding: 0.5rem 0;
	text-align: center;
	font-weight: 700;
	letter-spacing: 3px;
	letter-spacing: var(--mainSpacing);
	color: #fff;
}
/* end of room  */
/* single room*/

.single-room {
	padding: 5rem 0 0 0;
}
.single-room-images {
	width: 80vw;
	margin: 0 auto;
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
	grid-row-gap: 2rem;
	grid-column-gap: 50px;
