	letter-spacing: var(--mainSpacing);
}

.info h6 {
	font-weight: 300;
}
.room-extras {
	width: 80vw;
	margin: 0 auto 3rem auto;
}
.room-extras h6 {
	text-transform: capitalize;
	letter-spacing: 3px;
	letter-spacing: var(--mainSpacing);
