	border: 2px solid #f48244;
}

.orangeSearchbarSearchButton {
	display: flex;
	justify-content: flex-end;
	width: 90%;
	margin: 0 auto;

	border-radius: 5px;
}

@media screen and (min-width: 776px) {
	.roomslist-center {
		width: 70vw;
	}
}
@media screen and (min-width: 992px) {
	.roomslist {
		padding: 1rem 0;
		display: flex;
    flex-direction: row;
    background: white;
    background-color: white;
	}

	.roomslist-center {
		width: 70vw;
		/* margin: 0 auto; */
		margin-left: auto;
		/* margin-right: 5rem; */
		display: grid;
		grid-template-columns: repeat(auto-fill, minmax(270px, 1fr));
		grid-row-gap: 2rem;
